import React from 'react';
import styled from 'styled-components';

import { Table } from 'react-bootstrap';

const Positive = styled.td`
    background-color: #aaffaa;
`;
const Borderline = styled.td`
    background-color: #fff999;
`;
const Negative = styled.td`
    background-color: #ffbbbb;
`;

class ResultsTable extends React.Component {
    generateElement(lower, upper) {
        return function (value) {
            return  value < lower || isNaN(value) ? (<Positive>{isNaN(value) ? '-' : value}</Positive>)
                : value > upper ? (<Negative>{value}</Negative>)
                : (<Borderline>{value}</Borderline>);
        }
    }

    render () {
        const elementGenerator = this.generateElement(
            parseInt(this.props.lower, 10),
            parseInt(this.props.upper, 10)
        );

        return (
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>Gene Symbol</th>
                        <th>Ensembl ID</th>
                        <th>Transcript type</th>
                        <th>Panels</th>
                        <th>MRSD(Blood)</th>
                        <th>MRSD(LCL)</th>
                        <th>MRSD(Muscle)</th>
                    </tr>
                </thead>
                <tbody>
                { this.props.results.map(result => {
                    const blood = elementGenerator(parseInt(result['MRSD(Blood)'], 10));
                    const lcl = elementGenerator(parseInt(result['MRSD(LCL)'], 10));
                    const muscle = elementGenerator(parseInt(result['MRSD(Muscle)'], 10));
                
                    return (
                        <tr key={result.gene_symbol}>
                            <td>{result.gene_symbol}</td>
                            <td>{result.ensembl_id}</td>
                            <td>{result.transcript_type}</td>
                            <td>{result.panels}</td>
                            { blood }
                            { lcl }
                            { muscle }
                        </tr>
                    );}) }
                </tbody>
            </Table>
        );
    }
}

export default ResultsTable;
