import React from 'react';

import { Formik, Form } from 'formik';
import { Alert, Button, Tab, Tabs } from 'react-bootstrap';
import GeneList from '../Molecules/GeneList';

class NewSearch extends React.Component {
    parseGeneList(data) {
        return data
            .split("\n")
            .map(val => val.trim().toUpperCase())
            .filter(val => val !== '');
    }

    render() {
        const setSearchTerms = this.props.setSearchTerms;
        const component = this;

        return (
            <>
                <h2>New search</h2>
                <Formik
                    initialValues={{ geneList: '' }}
                    validate={ values => {
                        const errors = {};
                        if (component.parseGeneList(values.geneList).length === 0) {
                            errors.geneList = 'You must supply a list of genes to generate the data set';
                        }

                        return errors;
                    }}
                    onSubmit={(values, { setSubmitting }) => {
                        setSearchTerms({
                            geneList: component.parseGeneList(values.geneList)
                        });
                    }}
                >
                    {({
                        values,
                        errors,
                        touched,
                        handleChange,
                        handleBlur,
                        handleSubmit,
                        isSubmitting
                    }) => (
                        <Form>
                            <Tabs defaultActiveKey="geneListTab">
                                <Tab eventKey="geneListTab" title="Gene list" data-testid="geneListTab">
                                    <GeneList
                                        handleChange={handleChange}
                                        handleBlur={handleBlur}
                                        value={values.geneList}
                                    />
                                </Tab>
                            </Tabs>
                            { errors.geneList && touched.geneList &&
                                <Alert variant="danger" data-testid="error-message">
                                    { errors.geneList }
                                </Alert>
                            }
                            <Button variant="primary" type="submit">
                                Search
                            </Button>
                        </Form>
                    )}
                </Formik>
            </>
        );
    }
}

export default NewSearch;
