import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";
import './App.scss';
import { Container, Row, Col, Jumbotron } from "react-bootstrap";

import { readRemoteFile } from "react-papaparse";
import NewSearch from './Organisms/NewSearch';
import Results from './Organisms/Results';

class App extends React.Component {
    componentDidMount() {
        let component = this;

        readRemoteFile('./data/all-gencode-genes.master.short.tsv', {
            header: true,
            delimiter: "\t",
            complete: (parsed => component.setState({ data: parsed.data }))
        });
    }

    setSearchTerms(component) {
        return (data) => {
            component.setState({
                geneList: data.geneList
            });
        };
    }

    clearSearchTerms(component) {
        return () => {
            component.setState({
                geneList: null
            });
        };
    }

    getSearchResults(component) {
        return function () {
            if (!component.state || !component.state.data || !component.state.geneList) {
                return [];
            }

            return component.state.data
                .filter(item => {
                    return component.state.geneList.indexOf(item.gene_symbol) !== -1;
                });
        };
    }

    render() {
        return (
            <Router>
                <Container>
                    <Row>
                        <Col>
                            <Jumbotron>
                                <h1>MRSD Web Portal</h1>
                            </Jumbotron>
                        </Col>
                    </Row>

                    <Row>
                        <Col>
                            <Switch>
                                <Route
                                    path="/results"
                                    render={() => {
                                        return (
                                            !this.state || !this.state.geneList || !this.state.data ?
                                                <Redirect to="/" /> :
                                                <Results
                                                    clearSearchTerms={this.clearSearchTerms(this)}
                                                    getSearchResults={this.getSearchResults(this)}
                                                />
                                        );
                                    }}
                                />
                                <Route
                                    path="/"
                                    render={() => {
                                        return (
                                            this.state && this.state.geneList && this.state.data ?
                                                <Redirect to="/results" /> :
                                                <NewSearch
                                                    setSearchTerms={this.setSearchTerms(this)}
                                                />
                                        );
                                    }}
                                />
                            </Switch>
                        </Col>
                    </Row>
                </Container>
            </Router>
        );
    }
}

export default App;
